            aria-valuemax="100"
          ></div>
        </div>
      </div>
      <div class="col-md-2">
        <div class="row d-flex align-items-center">
          <div class="col-md-12">
            <h1>
              <span v-if="!order.is_paid" class="badge badge-white text-secondary">3</span>
              <span v-else class="badge badge-white text-impor">3</span>
            </h1>
            <h5 class="text-dark"></h5>
          </div>
        </div>
      </div>
    </div>
    <div class="row justify-content-center">
      <div class="card">
        <div class="card-body">
          <form @submit.prevent="payOrder">
            <div class="row">
              <div class="col-md-6 border-right pr-5">
                <h5 class="text-center py-3"></h5>
                <table class="table">
                  <thead>
                    <th width="500px"></th>
                    <th width="100px"></th>
                    <th></th>
                  </thead>
                  <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                      <td class="align-middle">{{ item.product.title }}</td>
                      <td class="align-middle">X {{ item.qty }}</td>
                      <td class="align-middle text-right">{{ item.final_total | currency}}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colspan="2" class="text-right"></td>
                      <td class="text-right">{{ order.total | currency}}</td>
