<script>
export default {
  data() {
    return {
      order: {
        user: {}
      },
      orderId: "",
      date: new Date()
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `https://vue-course-api.hexschool.io/api/sasa/order/${vm.orderId}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then(response => {
        vm.order = response.data.order;
        console.log(response);
        vm.$store.dispatch('updateLoading', false);
      });
    },
    payOrder() {
      const vm = this;
      const api = `https://vue-course-api.hexschool.io/api/sasa/pay/${vm.orderId}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.post(api).then(response => {
        console.log(response);
        if (response.data.success) {
          vm.getOrder();
        }
        vm.$store.dispatch('updateLoading', false);
      });
    },
    dateFormat(time) {
      var date = new Date(time);
      var year = date.getFullYear();
      /* 00
       * 100   09:11:05
       * */
      var month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      // 
      return year + "-" + month + "-" + day + " ";
    }
  },
  mounted() {
    let _this = this; // Vuethis
    this.timer = setInterval(() => {
      _this.date = new Date(); // date
    }, 1000);
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // Vue
    }
  },
  computed: {
    isLoading(){
      return this.$store.state.isLoading;
    }
  },
  created() {
    this.orderId = this.$route.params.orderId;
    this.getOrder();
    console.log(this.orderId);
  }
};
</script>
